import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Promo from '../Promo/Promo';
import Game from '../Game/Game';
import Slider from '../Slider/Slider';
import './_Main.scss';

function Main({ games, path, pages }) {
  const [isActive, setIsActive] = useState(0);
  const currentGameSection = games.filter((item) => path === item.url);
  const len = currentGameSection.length - 1;
  function handleLeftButtonClick() {
    setIsActive(isActive < 1 ? len : isActive - 1);
  }
  function handleRightButtonClick() {
    setIsActive(isActive === len ? 0 : isActive + 1);
  }

  return (
    <main className='main'>
      {pages.map((page) => {
        let component;
        if (path === `${page.url}`) {
          component = (
            <Promo
              key={page.id}
              id={page.id}
              url={page.url}
              heading={page.heading}
              cover={page.cover}
              coverSmall={page.coverSmall}
              text={page.text}
              name='promo'
              mix=''
            />
          );
        }
        return component;
      })}
      <Slider
        games={games}
        handleLeftButtonClick={handleLeftButtonClick}
        handleRightButtonClick={handleRightButtonClick}>
        {currentGameSection.map((game, index) => {
          const component = (
            <Game
              isActive={isActive}
              key={index}
              index={index}
              id={game.id}
              url={game.url}
              heading={game.heading}
              cover={game.cover}
              text={game.text}
              year={game.year}
              platforms={game.platforms}
              name='game'
            />
          );

          return component;
        })}
      </Slider>
    </main>
  );
}

export default Main;

Main.propTypes = {
  games: PropTypes.array,
  pages: PropTypes.array,
  path: PropTypes.string,
};
