import React from 'react';
import PropTypes from 'prop-types';
import Button from '../Button/Button';
import './_Slider.scss';

function Slider(props) {
  return (
    <div className='slider' id="slider">
      {props.children}
      <Button
        mix={'slider__button slider__button_left'}
        type='button'
        name='slider-button'
        aria-label='slider-button'
        text=''
        onClick={props.handleLeftButtonClick}
        ternary=''
      />
      <Button
        mix={'slider__button slider__button_right'}
        type='button'
        name='slider-button'
        aria-label='slider-button'
        text=''
        onClick={props.handleRightButtonClick}
        ternary=''
      />
    </div>
  );
}

export default Slider;
Slider.propTypes = {
  handleRightButtonClick: PropTypes.func,
  handleLeftButtonClick: PropTypes.func,
  children: PropTypes.node,
};
