import React from 'react';
import PropTypes from 'prop-types';
import { HashLink as Link } from 'react-router-hash-link';
import Section from '../Section/Section';

import Paragraph from '../Paragraph/Paragraph';
import './_Promo.scss';

function Promo(props) {
  return (
    <Section name={props.name} id={props.id} mix={props.mix}>
      <picture className='promo__picture'>
        <source srcSet={props.coverSmall} media="(max-width: 768px)" />
        <source srcSet={props.cover} />
        <img className='promo__image' src={props.cover} alt={props.heading} />
      </picture>
      <article className={`${props.name}__info`}>
        <h1 className={`${props.name}__heading`}>{props.heading}</h1>

        <div className={`${props.name}__text`}>
          {props.text.map((item) => (
            <Paragraph name='promo' key={item.length} text={item} />
          ))}
        </div>
      </article>
      <Link to={`${props.url}#slider`} className='promo__link'>

      </Link>
    </Section>
  );
}
export default Promo;

Promo.propTypes = {
  name: PropTypes.string,
  mix: PropTypes.string,
  url: PropTypes.string,
  id: PropTypes.string,
  cover: PropTypes.string,
  coverSmall: PropTypes.string,
  text: PropTypes.array,
  heading: PropTypes.string,
};
